function form() {
    $("form").submit(function(e) {
        e.preventDefault();
        $.ajax({
            type: "POST",
            url: "mailer/smart.php",
            data: $(this).serialize()
        }).done(function() {
            $(this).find("input").val("");
            $('#main_modal').fadeOut();
            $('#thanks, .overlay').fadeIn('slow');
            $("form").trigger("reset");
        });
        return false;
    });
}

module.exports = form;